<template>
  <div class="cell pic" @mouseenter="showText = true" @mouseleave="showText = false">
    <div class="text" v-if="member != undefined" :class="{active: showText}">
      <span class="name">{{member.name}}</span>
      <br/>
      {{member.group}}
      <br/>
      {{member.grade}}
      <br/>
      {{member.major}}
    </div>
    <img class="pic_img" v-bind:src="member.pic" v-if="member!=undefined"/>
  </div>
</template>
<script>
  export default {
    name: 'teamMember',
    props: ['member'],
    data () {
      return {
        showText: false
      }
    }
  }
</script>

<style scoped>
.cell{
  float: left;
  width: 33.33%;
  margin: 0;
  padding: 0;
  border: 0;
  position: relative;
  overflow: hidden;
  padding-bottom: 33.33%;
}
.text{
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  width: 100%;
  background-color: rgba(0,0,0,0.8);
  height: 100%;
  font-size: 1.3vw;
  text-align: center;
  color: white;
  font-weight: 300;
  vertical-align: middle;
  visibility: hidden;
  z-index: 2;
}
.name {
  font-size: 2vw;
  font-weight: 400;
  margin: 4px 0;
  display: inline-block;
}
.pic_img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  margin: 0;
  display: block;
}
.active{
  visibility: visible;
}
@media screen and (min-width: 990px){
  .text {
    padding-top: 10px;
  }
}
@media screen and (max-width: 768px){
  .name {
    font-size: 7vw;
  }
  .text {
    font-size: 4vw;
  }
}
</style>


