<template>
			<div class="card">
				<div class="card_1">
					<div class="card_photo1">
					</div>
				</div>
				<div class="card_2">
						<div class="wjby card_weijin">
							<p >卫津路</p>
							<img src="../assets/weijin.jpg"/>
						</div>
						<div class="card_ticket">
							<img class="bg" src="../assets/ticket.png"/>
							<div class="ticket_ad">
								<p class="adTitle">{{ad.title}}</p>
								<div class="line1"></div>
								<p class="adDesc">{{ad.desc}}</p>
							</div>
							<div class="qrcode">
								<img class="qrcode_bg" src="../assets/qrcode_bg.png"/>
								<img class="qrcode_img" :src="ad.Qrcode"/>
							</div>
						</div>
						<div class="wjby card_beiyang">
							<p>北洋园</p>
							<img src="../assets/beiyang.jpg"/>
						</div>
					
				</div>
			</div>
</template>

<script>
  export default {
    name: 'indexCard',
    data () {
      return {
        ad: {
          // title: '2017年天外天工作室春季纳新开始了',
          // desc: '本次纳新只面向北洋园，卫津路的同学们下学期再见啦~',
          // Qrcode: require('../assets/Qrcode.png'),
          // url: 'https://www.twt.edu.cn/'
        }
        // qr: {
        //   value: 'https://www.twt.edu.cn/',
        //   padding: 10,
        //   size: 78,
        //   background: '#00a1e9',
        //   backgroundAlpha: 0,
        //   level: 'H',
        //   mime: 'image/png'
        // }
      }
    },
    methods: {
      init: function () {
        this.$http.get('Qrcode')
        .then(function (response) {
          this.ad = response.data
        })
        // this.qr.value = this.ad.url
      }
    },
    watch: {
      $route: 'init'
    },
    created () {
      this.init()
    }
  }
</script>

<style scoped>
.card {
	width: 100%;
	overflow: hidden;
}
.card_1 {
	background-color: #f0f2f2;
	padding-bottom: 60px;	
}
.card_photo1 {
	width: 100%; 
	height: 200px;
	background: url(../assets/card_1.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
}
.card_2 {
	width: 100%;
	background-color: #F8F8F8;
	padding-bottom: 30px;
}
.card_2:after {       
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    height: 0;
    font-size:0;
}
.card_weijin {
	width: 25%;
	text-align: center;
	margin-left: 8%;
	position: relative;
	float: left;
}
.wjby img{
	width: 100%;
}
.wjby p{
	position: absolute;
	top: -53px;
	color: #779ea3;
	line-height: 20px;
	font-size: 13px;
	width: 100%;
	text-align: center;
}
.card_beiyang {
	width: 25%;
	text-align: center;
	margin-right: 8%;
	position: relative;
	float: left;
}
.card_ticket {
	width: 30%;
	position: relative;
	float: left;
	margin: 0 2%;
}
.bg{
	width: 100%;
	position: relative;
	top: -17px;
}
.qrcode {
	position: absolute;
	width: 20%;
	right: 3%;
	bottom: 30px;
	overflow: hidden;
}
.qrcode_bg {
	width: 100%;
}
.qrcode_img {
	position: absolute;
	top: 15%;
	left: 15%;
	width: 70%;
	opacity:0.6;
}
.ticket_ad {
	position: absolute;
	width: 100%;
	top: 50%;
	padding: 0 23% 0 8%;
	text-align: left;
	box-sizing: border-box;
}
.ticket_text{
	position: absolute;
	top: 0;
	bottom: 0;
	margin: auto
}
.adTitle {
	font-size: 1.2vw;
	color: #444444;
	margin: 0;
}
.adDesc {
	font-size: 0.9vw;
	color: #999999;
	margin: 0;
}
.line1 {
	height: 1px;
	width: 100%;
	box-sizing: border-box;
	background-color: #c4c5c8;
}
@media screen and (max-width: 990px){
	.ticket_ad {
		top: 40%;
	}
	.QRCode {
	}
}
@media screen and (max-width: 768px){
	.wjby {
		display: none;
	}
	.card_ticket {
		width: 80%;
		margin: 0 10%;
	}
	.ticket_ad {
		top: 50%;
	}
	.adTitle {
		font-size: 3.3vw;
	}
	.adDesc {
		font-size: 2.2vw;
	}
}
@media screen and (max-width: 350px){
	.ticket_ad {
		top: 40%;
	}
}
</style>


