            			<p>培养优秀毕业生近<span class="number">两万</span>人</p>
            			<p>线上服务器<span class="number">22</span>台</p>
            			<p>新老校区工作室面积近<span class="number">300</span>平米</p>
        			</div>
				</div>
			</div>
			<div class="connect">
				<p class="connect_title">欢迎咨询邮箱<a href="mailto:admin@twtstudio.com">admin@twtstudio.com</a>，或者去我们的站点反馈区反馈问题</p>
