					<div class="division_card">
						<img src="../assets/division_frontend.png"/>
						<p class="division_name">前端开发</p>
						<hr/>
						<p class="division_desc">
							负责工作室项目的前端开发工作<br/>与设计师和后台程序配合<br/>高效率高质量的完成页面的实现工作<br/>负责工作室各产品体验改进和界面技术优化</p>
