    components: { navWhite, footerBlack },
    data () {
      return {
        // introduction: [{}, {}, {}];
        binner: require('../assets/aboutUs_binner.jpg'),
        items: []
      }
    },
    methods: {
      init: function () {
        window.scrollTo(0, 0)
        this.$http.get('milestones')
        .then(function (response) {
          this.items = response.data
        })
        // this.$http.get('introduce')
        // .then(function (response) {
        //   this.introduction = response.data
        // })
