<template>
    <div class="carousel">
      <router-link :to="{path: '/activities'}">
        <transition-group tag='ul' class="clearfix slide" name='image'>
            <li v-for='(activity, index) in activities' :key='index' v-show='index===mark'>
                <img :src="activity.pic">
                <div class="fade"></div>
                <div class="text">
                  <p class="name">{{activity.title}}</p>
                  <hr/>
                  <p class="desc">{{activity.desc}}<br/>{{activity.place}}</p>
                </div>
            </li>
        </transition-group>
        <div class="bullet">
            <span class="circle" v-for='(item,index) in activities.length' :class="{'active':index===mark}" @click='change(index)'></span>
        </div>
      </router-link>
    </div>
</template>

<script>
  export default {
    name: 'indexTopPicture',
    props: ['activities'],
    data () {
      return {
        mark: 0
      }
    },
    methods: {
      change: function (index) {
        this.mark = index % this.activities.length
      },
      autoPlay: function () {
        this.mark++
        if (this.mark === this.activities.length) {
          this.mark = 0
          return
        }
      },
      play: function () {
        setInterval(this.autoPlay, 5000)
      }
    },
    created () {
      this.play()
    }
  }
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
.clearfix {
  content: '';
  clear: both;
  display: block;
}
.carousel {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
}
.slide {
  width: 100%;
  height: 100%;
}
.slide li {
  position: absolute;
  width: 100%;
  height: 100%;
}
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.fade {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(38,38,40,0.6);
}
.bullet {
  position: absolute;
  bottom: 8%;
  margin-left: 10%;
}
.circle {
  width: 1vw;
  height: 1vw;
  border: 0;
  background-color: rgba(255,255,255,0.6);
  display: inline-block;
  -webkit-border-radius: 0.5vw;
  -moz-border-radius: 0.5vw;
  -o-border-radius: 0.5vw;
  border-radius: 0.5vw;
  margin-left: -0.5vw;
  z-index: 4;
  cursor: pointer;
}
.active {
  background-color: rgba(255,255,255,1);
}
.image-enter-active {
  transition: all .6s ease;
}
.image-leave-active {
  transition: all .6s ease;
}
.image-enter, .image-leave-to{
  opacity: 0;
}
.text {
  position: absolute;
  top: 30%;
  color: white;
  width: 100%;
  text-align: center;
}
.name {
  font-size: 3vw;
  width: 100%;
  text-align: center;
  margin-bottom: 3%
}
.desc {
  font-size: 1.5vw;
  line-height: 200%;
  margin: 3%
}
.text hr {
  width: 10%;
  height: 2px;
  border: 0;
  background-color: white;
  margin: 0 auto;
}
@media screen and (max-width: 768px){
  .text {
    top: 20%;
  }
  .name {
    font-size: 5vw;
  }
  .desc {
    font-size: 2vw;
  }
  .bullet span {
    width: 1.4vw;
    height: 1.4vw;
    -webkit-border-radius: 0.7vw;
    -moz-border-radius: 0.7vw;
    -o-border-radius: 0.7vw;
    border-radius: 0.7vw;
    margin-left: -0.7vw;
}
}
</style>


