<template>
	<div class="sendMessage">
		<p class="sendMessage_notice">
			有什么想对我们说的？给我们留言吧！你的留言会在寄语动态中显示哦
		</p>
		<div class="form">
			<p>邮箱</p>
			<input type="text" name="email" placeholder=" hi，请留下您的邮箱" v-model="send_text.email" autocomplete="off">
			<p>姓名</p>
			<input type="text" name="name" placeholder=" 你叫什么名字呢" v-model="send_text.name" autocomplete="off">
			<p>留言内容</p>
			<textarea name="text" placeholder=" 有什么想对我们说的，在这里留言吧…" v-model="send_text.text" autocomplete="off">
			</textarea>
			<br/>
			<button @click="sendText">留言</button>
		</div>
	</div>
</template>

<script>
  import navWhite from '@/components/nav'
  import footerBlack from '@/components/footer'
  export default {
    name: 'sendMessage',
    components: { navWhite, footerBlack },
    data () {
      return {
        send_text: {
          name: '',
          email: '',
          text: ''
        }
      }
    },
    methods: {
      sendText: function () {
        // var formData = JSON.stringify(this.send_text)
        // console.log(this.send_text)
        var myreg = /^(?:\w+\.?)*\w+@(?:\w+\.)*\w+$/
        if (myreg.test(this.send_text.email)) {
          this.$http.post('form', this.send_text).then((res) => {
            console.log(res)
            this.send_text.name = ''
            this.send_text.email = ''
            this.send_text.text = ''
          })
        } else {
          alert('请输出入正确的邮箱')
        }
      }
    }
  }
</script>

<style scoped>
.sendMessage {
	background-color: #2f3033;
	border-bottom: 1px solid #434447;
	color: #7f7f80;
	overflow: hidden;
	text-align: center;
}
.form {
	width: 90%;
	max-width: 450px;
	margin: auto;
}
.form p {
	margin: 20px 0 10px 0;
	font-size: 13px;
	text-align: left;
}
.sendMessage_notice {
	margin: 40px auto;
	font-size: 14px;
	width: 60%;
	max-width: 300px;
	text-align: center;
}
.sendMessage input {
	border: 1px solid #444444;
	width: 100%;
	height: 30px;
	background-color:transparent;
	-webkit-border-radius: 2px;
	-moz-border-radius: 2px;
	-o-border-radius: 2px;
	border-radius: 2px;
	box-sizing: border-box;
	color: #7f7f80;
	outline:none;
	transition: all 0.5s;
}
.sendMessage input:focus {
	box-shadow: 0px 0px 10px #444444
}
.sendMessage textarea {
	width:  100%;
	height: 100px;
	border: 1px solid #444444;
	background-color:transparent;
	-webkit-border-radius: 2px;
	-moz-border-radius: 2px;
	-o-border-radius: 2px;
	border-radius: 2px;
	box-sizing: border-box;
	resize:none;
	color: #7f7f80;
	outline:none;
	transition: all 0.5s;
}
.sendMessage textarea:focus {
	box-shadow: 0px 0px 10px #444444
}
.sendMessage button {
	-webkit-border-radius: 20px;
	-moz-border-radius: 20px;
	-o-border-radius: 20px;
	border-radius: 20px;
	height: 40px;
	width: 150px;
	background: -webkit-linear-gradient(left, #05D5EE, #0081E5);
	background: -o-linear-gradient(right, #05D5EE, #0081E5); 
	background: -moz-linear-gradient(right, #05D5EE, #0081E5); 
	background: linear-gradient(to right, #05D5EE , #0081E5); 
	color: white;
	font-size: 13px;
	font-weight: 300;
	border: 0;
	margin: 30px auto;
	cursor: pointer;
	outline: none;
	transition: all 0.5s;
}
.sendMessage button:hover {
  box-shadow: 0px 0px 10px #00a1e9;
}
</style>


