<template>
		<div class="activities" id="activities">
			<navWhite></navWhite>
			<div class="banner">
				<img v-bind:src="banner"/>
			</div>
			<div class="main">
				<p class="title">活动动态</p>
				<hr class="hr">
				<div class="activities_container">
					<router-link class="activity" v-for="(activity, index) in activities" :to="{path: '/activity/'+ activity.id}" v-bind:id="'activity'+index" v-if="showMore || index < 5" v-bind:href="activity.link" target="_blank" v-bind:activityid="activity.id">
						<div class="activity_pic">
							<img v-bind:src="activity.image"/>
						</div>
						<div class="activity_text">
							<p v-bind:title="activity.name" class="activity_name">{{activity.name}}</p>
							<p class="activity_time">{{activity.time}}</p>
							<div v-bind:title="activity.content" readonly="readonly" class="activity_desc">{{activity.content}}</div>
						</div>
					</router-link>
					<p class="more" @click="showMore = true" v-if="(activities.length > 5) && (!showMore)">查看更多</p>
				</div>
			</div>
			<footerBlack></footerBlack>
		</div>
</template>

<script>
  import navWhite from '@/components/nav'
  import footerBlack from '@/components/footer'
  export default {
    name: 'activities',
    components: { navWhite, footerBlack },
    data () {
      return {
        activities: [],
        banner: require('../assets/0919_2.jpg'),
        showMore: false
      }
    },
    methods: {
      init: function () {
        window.scrollTo(0, 0)
        this.showMore = false
        this.$http.get('activity')
        .then(function (response) {
          this.activities = response.data
        })
      }
    },
    watch: {
      $route: 'init'
    },
    created () {
      this.init()
    }
  }
</script>

<style scoped>
.activities{
	background-color: #F8F8F8;
	min-height: 100vh;
	overflow: hidden;
	width: 100%;
	position: relative;
	padding-bottom: 80px;
	box-sizing: border-box;
}
.banner {
	width: 100%;
	margin-top: 60px;
}
.banner img {
	width: 100%;
}
.main {
	width: 76%;
	padding: 30px 12%;
	overflow: hidden;
}
.title {
	font-size: 23px;
	color: #333333;
	width: 100%;
	text-align: center;
	margin:  3vw auto 1vw auto;
}
.hr {
	width: 65px;
	height: 2px;
	border: 0;
	margin: 0 auto;
	margin-bottom: 30px;
	background: -webkit-linear-gradient(left, #05D5EE, #0081E5);
	background: -o-linear-gradient(right, #05D5EE, #0081E5); 
	background: -moz-linear-gradient(right, #05D5EE, #0081E5); 
	background: linear-gradient(to right, #05D5EE , #0081E5); 	
}
@media screen and (max-width: 768px){
	.title {
		font-size: 20px;
	}
	.hr {
		width: 50px;
	}
}
.activities_container {
	width: 100%;
	overflow: hidden;
}
.activity {
	width: 100%;
	overflow: hidden;
	display: block;
	margin-bottom: 3vw;
}
.activity_pic {
	float: left;
	width: 35%;
	margin: 0;
	margin-right: 3%;
}
.activity_pic img{
	width: 100%;
}
.activity_text {
	float: left;
	width: 62%;
}
.activity_name {
	font-size: 2vw;
	margin: 1vw 0;
	color: #333333;
	text-overflow: -o-ellipsis-lastline;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1;
	-webkit-box-orient: vertical;
}
.activity_time {
	font-size: 1.4vw;
	margin: 0.9vw 0;
	color: #94a1b4;
}
.activity_desc {
	font-size: 1.5vw;
	margin: 0;
	color: #999999;
	width: 100%;
	text-overflow: -o-ellipsis-lastline;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
}
.more {
	color: #999999;
	float: left;
	margin: auto;
	width: 100%;
	text-align: center;
	font-size: 12px;
	cursor: pointer;
}
@media screen and (max-width: 768px){
	.main {
		padding: 0;
		width: 100%;
	}
	.activity {
		margin-bottom: 15vw;
	}
	.activity_pic {
		width: 100%;
	}
	.activity_text {
		width: 80%;
		margin: 0 10%;
	}
	.activity_name {
		font-size: 20px;
		margin: 1vw 0;
		text-overflow: -o-ellipsis-lastline;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
	}
	.activity_time {
		font-size: 15px;
		margin: 0.9vw 0;
		color: #94a1b4;
	}
	.activity_desc {
		font-size: 17px;
		margin: 0;
		color: #999999;
		width: 100%;
		text-overflow: -o-ellipsis-lastline;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
	}
	.more {
		font-size: 14px;
		margin-bottom: 20px;
	}
}
@media screen and (max-width: 768px) {
	.activities {
		padding-bottom: 80px;
	}
}
</style>


