<template>
	<div id="ourAPP" class="ourAPP">
		<p class="APP_title">下载我们的APP</p>
		<hr/>
		<div class="apps">
			<div class="wby">
				<img src="../assets/app_wby.jpg"/>
				<p class="appName">微北洋APP-最懂你的校园助手</p>
				<p class="appType">IOS and Android</p>
				<button onclick="window.open('http://mobile.twt.edu.cn/wpy/3.html')">DOWNLOAD</button>
			</div>
			<div class="others">
				<div class="bbs" onclick="window.open('http://mobile.twt.edu.cn/bbs')">
					<img src="../assets/app_bbs.jpg"/>
					<p class="appName">求实BBS-社区达人等你吐槽</p>
					<p class="appType">IOS and Android</p>
				</div>
				<div class="wj" onclick="window.open('http://mobile.twt.edu.cn/wenjin')">
					<img src="../assets/app_wj.jpg">
					<p class="appName">问津APP-天大人的问答社区</p>
					<p class="appType">IOS and Android</p>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
  export default {
    name: 'indexAPP'
  }
</script>

<style scoped>
.ourAPP {
	width: 100%;
	overflow: hidden;
	background-color: white;
	margin-bottom: 40px;
}
.APP_title {
	color: #2d3b3e;
	font-size: 20px;
	margin: 20px 0 10px 0;
	text-align: center;
}
.ourAPP hr{
	width: 50px;
	height: 2px;
	border: 0;
	margin: 0 auto;
	margin-bottom: 20px;
	background: -webkit-linear-gradient(left, #05D5EE, #0081E5);
	background: -o-linear-gradient(right, #05D5EE, #0081E5); 
	background: -moz-linear-gradient(right, #05D5EE, #0081E5); 
	background: linear-gradient(to right, #05D5EE , #0081E5); 
}
.apps {
	width: 86%;
	overflow: hidden;
	margin: 0 7%;
	position: relative;
}
.wby {
	width: 57.6%;
	margin: 0 2.5%;
	position: relative;
}
.wby img {
	width: 100%;
	height: 80%;
	margin: 0;
	box-shadow: 0 7px 30px #babed5;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-o-border-radius: 3px;
	border-radius: 3px;
}
.wby button{
	-webkit-border-radius: 1.5vw;
	-moz-border-radius: 1.5vw;
	-o-border-radius: 1.5vw;
	border-radius: 1.5vw;
	background: -webkit-linear-gradient(left, #05D5EE, #0081E5);
	background: -o-linear-gradient(right, #05D5EE, #0081E5); 
	background: -moz-linear-gradient(right, #05D5EE, #0081E5); 
	background: linear-gradient(to right, #05D5EE , #0081E5); 
	color: white;
	font-weight: 300;
	border: 0;
	margin: 30px auto;
	cursor: pointer;
	outline: none;
	height: 2.4vw;
	width: 8vw;
	font-size: 0.8vw;
	position: absolute;
	bottom: 25%;
	left:  12.5%;
	min-width: 80px;
	min-height: 18px;
}
.others {
	width: 32.4%;
	right: 0;
	height: 100%;
	margin: 0 2.5%;
	position: absolute;
	right: 0;
	top: 0;
	overflow: hidden;
}
.others img {
	width: 100%;
	right: 0;
	height: 60%;
	margin: 0;
	box-shadow: 0 5px 30px #babed5;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-o-border-radius: 3px;
	border-radius: 3px;
}
.bbs {
	height: 50%;
	width: 100%;
	cursor: pointer;
	float: left;
}
.wj {
	height: 50%;
	width: 100%;
	cursor: pointer;
	float: left;
}
.appType {
	font-size: 12px;
	color: #80888a;
}
.appName {
	font-size: 16px;
	color: #2d3b3e;
}
@media screen and (max-width: 768px) {
	.wby {
		width: 80%;
		margin: 0 10%;
	}
	.others {
		position: relative;
		width:  80%;
		margin: 0 10%;
	}
	.bbs {
		width: 100%;
		margin: 0;
		height: auto;
	}
	.wj {
		width: 100%;
		margin: 0;
		height: auto;
	}
	.wby button {
		-webkit-border-radius: 2.1vw;
		-moz-border-radius: 2.1vw;
		-o-border-radius: 2.1vw;
		border-radius: 2.1vw;
		height: 4.2vw;
		width: 12vw;
		font-size: 12px;
		bottom: 30%;
	}
}
</style>


