<template>
	<div class="join">
		<div class="nav" id="nav">
			<div class="navButton" @click="show = !show"></div>
			<router-link :to="{path: '/index'}" id="nav-logo"><img src="../assets/twtlogo.png"></router-link>
   			<div class="nav-list" :class="{show: show}">
   				<ul>
   					<li><router-link :to="{path: '/index'}" class="nav-tag">首页</router-link></li>
   					<li><router-link :to="{path: '/aboutUs'}" class="nav-tag">关于我们</router-link></li>
   					<li><router-link :to="{path: '/works'}" class="nav-tag">项目案例</router-link></li>
            		<li><router-link :to="{path: '/activities'}" class="nav-tag">活动动态</router-link></li>
   					<li><router-link :to="{path: '/team'}" class="nav-tag">团队成员</router-link></li>
   					<li id="special"><router-link :to="{path: '/join'}" class="nav-tag">加入我们</router-link></li>
    			</ul>
    		</div>
		</div>
		<hr/>
		<div class="main">
			<div class="white1">
			</div>
			<div class="white2">
			</div>
			<div class="white3">
				<!--
				<p>hello</p>
				<p>The most beautiful time to meet the most beautiful you<br/><br/>Do you want to join us ?</p>
				-->
			</div>
			<button onclick="window.open('https://recruit.twtstudio.com')">前去招募中心</button>
		</div>
		<footerBlack class="footer"></footerBlack>
		<div :class="{'hide':show === false}" class="fade" @click="show = false"></div>
	</div>
</template>

<script>
  import footerBlack from '@/components/footer'
  export default {
    components: { footerBlack },
    name: 'join',
    data () {
      return {
        show: false
      }
    }
  }
</script>

<style scoped>
.join {
	height: 100%;
	background: url(../assets/join_bg.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	position: relative;
	min-height: 100vh;
	overflow: hidden;
	width: 100%;
	padding-bottom: 80px;
	box-sizing: border-box;
}
  .navButton {
    background-clip: content-box;
    width: 20px;
    height: 4px;
    border-top: 4px solid #00a1e9;
    border-bottom: 4px solid #00a1e9; 
    background-color: #00a1e9; 
    padding: 4px 0; 
    display: none;
  }
  .nav{
    width: 100%;
    height: 60px;
    font-size: 13px;
    background: transparent;
    color: white;
    position: absolute;
    top: 0;
    z-index: 4;
  }
  .nav img{
    float: left;
    margin-left: 5%;
    height: 100%;
    width: auto;
  }
  .nav-list{
    float: right;
    margin-right: 5%;
    width: 500px;
    z-index: 4
  }
  .nav-list ul{
    margin-top: 20px;
    border: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
  }
  .nav-list li{
    height: 19px;
    float: left;
    width: 16.66%;
    text-align: center;
    position: relative;
  }
  .nav-list a{
    line-height: 13px;
    transition: color 0.5s ease-in-out;
    -moz-transition: color 0.5s ease-in-out;
    -webkit-transition: color 0.5s ease-in-out; 
    -o-transition: color 0.5s ease-in-out; 
  }
  .nav-list a:hover{
    color: #05D5EE;
  }
  .nav-tag {
    color: white;
  }
  .router-link-active {
    color: #05D5EE;
  }
  @media screen and (max-width: 768px){
    .nav img {
      margin-left: 0;
    }
    .navButton {
      display: block;
      margin: 20px 5% 0 5%;
      float: left;
      cursor: pointer;
    }
    .nav-list a:hover{
      color: #00a1e9;
    }
    .nav-tag {
      color: black;
    }
    .router-link-active {
      color: #00a1e9;
    }
    .nav-list {
      position: absolute;
      left: 0;
      top: 60px;
      width: 100%;
      background-color: white;
      box-shadow: 0px 5px 15px rgba(0,0,0,0.15);
      padding: 0;
      height: 0;
      opacity: 0;
      overflow: hidden;
      transition:all 0.5s;
      -moz-transition:all 0.5s; /* Firefox 4 */
      -webkit-transition:all 0.5s; /* Safari and Chrome */
      -o-transition:all 0.5s; /* Opera */
    }
    .show{
      height: 200px;
      opacity: 100;
    }
    .nav-list ul {
      width: 100%;
      margin: 0;
      padding: 10px 0;
    }
    .nav-list li {
      width: 100%;
      display: block;
      height: 29px;
      transition: all .3s;  
      border-bottom: 1px solid #f0f2f2;
    }
    #special {
      border-bottom: 0;
    }
    .nav-list a{
      line-height: 30px;
    }
  }
.fade {
	height: 100%;
	width: 100%;
	background-color: rgba(0,0,0,0.3);
	position: fixed;
	top: 0;
	z-index: 1;
     transition:all 0.5s;
     -moz-transition:all 0.5s; /* Firefox 4 */
     -webkit-transition:all 0.5s; /* Safari and Chrome */
     -o-transition:all 0.5s; /* Opera */
}
.hide {
	background-color: rgba(0,0,0,0);
	z-index: -1
}
.join hr {
	height: 1px;
	width: 90%;
	margin: 0 auto;
	background-color: #4da8f2;
	border: 0;
}
.main {
	position: absolute;
	width: 280px;
	height: 320px;
	margin: auto;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
}
.white1{
	width: 240px;
	height: 220px;
	background-color: white;
	filter: alpha(Opacity=30);
	-moz-opacity:0.3;
	opacity: 0.3;
	box-shadow: 0px 0px 5px white;
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	-o-border-radius: 10px;
	border-radius: 10px;
	position: absolute;
	top: 0px;
	left: 20px;
}
.white2{
	width: 260px;
	height: 210px;
	background-color: white;
	filter: alpha(Opacity=50);
	-moz-opacity:0.5;
	opacity: 0.5;
	margin: auto;
	box-shadow: 0px 0px 5px white;
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	-o-border-radius: 10px;
	border-radius: 10px;
	position: absolute;
	top: 5px;
	left: 10px;
}
.white3{
	width: 280px;
	height: 200px;
	margin: auto;
	background: url(../assets/join_main.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	-o-border-radius: 10px;
	border-radius: 10px;
	position: absolute;
	top: 10px;
	left: 0px;
}
.main button {
	-webkit-border-radius: 20px;
	-moz-border-radius: 20px;
	-o-border-radius: 20px;
	border-radius: 20px;
	height: 40px;
	width: 180px;
	background: -webkit-linear-gradient(left, #05D5EE, #0081E5);
	background: -o-linear-gradient(right, #05D5EE, #0081E5); 
	background: -moz-linear-gradient(right, #05D5EE, #0081E5); 
	background: linear-gradient(to right, #05D5EE , #0081E5); 
	color: white;
	font-size: 12px;
	font-weight: 200;
	border: 2px solid white;
	margin: 0 auto;
	position: absolute;
	bottom: 0;
	left: 50px;
	cursor: pointer;
  transition: all 0.5s;
}
.main button:hover {
  box-shadow: 0px 0px 10px white;
}
</style>


