<template>
	<div class="index" id="index">
		<transition name="top">
			<indexTop id="top"></indexTop>
		</transition>
		<transition name="others">
			<div class="others">
				<div v-if="status == 1">
					<navWhite></navWhite>
				</div>
				<indexAboutUs></indexAboutUS>
				<indexProject></indexProject>
				<indexCard></indexCard>
				<indexAPP></indexAPP>
				<indexMessage></indexMessage>
				<div class="send_add">
					<div class="more_space"></div>
					<p>想什么呢？</p>
				</div>
				<sendMessage></sendMessage>
				<footerBlack></footerBlack>
			</div>
		</transition>
	</div>
</template>

<script>
  import navWhite from '@/components/nav'
  import indexTop from '@/components/indexTop'
  import indexAboutUs from '@/components/indexAboutUs'
  import indexAPP from '@/components/indexAPP'
  import indexCard from '@/components/indexCard'
  import indexProject from '@/components/indexProject'
  import indexMessage from '@/components/indexMessage'
  import sendMessage from '@/components/sendMessage'
  import footerBlack from '@/components/footer'
  export default {
    components: { navWhite, indexTop, indexAboutUs, indexAPP, indexCard, indexProject, indexMessage, sendMessage, footerBlack },
    name: 'index',
    data () {
      return {
        status: 0
      }
    },
    mounted () {
      window.addEventListener('scroll', this.scrollRun) // 监听滚动
    },
    methods: {
      scrollRun: function () {
        this.status = 1
        // console.log(document.documentElement.scrollTop)
        if (document.documentElement.scrollTop === 0) {
          this.status = 0
        }
      },
      init: function () {
        window.scrollTo(0, 0)
      }
    },
    watch: {
      $route: 'init'
    },
    created () {
      this.init()
    }
  }
</script>

<style scoped>
.index {
	width: 100%;	
	min-height: 100vh;
	overflow: hidden;
	position: relative;
}
.send_add {
	position: relative;
}
.more_space {
	width: 100%;
	height: 30px;
	background-color: #2f3033;
}
.send_add p{
	margin: 0;	
	width: 100%;
	height: 30px;
	background-color: #2f3033;
	font-size: 18px;
	letter-spacing: 2px;
	font-weight: 300;
	color: #f8f8f8;
	text-align: center;
	position: absolute;
	top: 40px;
}
.others {
	//display: none;
	padding-bottom: 80px;
}
@media screen and (max-width: 768px) {
	.others {
		padding-bottom: 80px;
	}
}
</style>


