<template>
		<div class="activity" id="activity">
			<navWhite></navWhite>
      <div class="main" v-html="x">
      </div>
			<footerBlack></footerBlack>
		</div>
</template>

<script>
  import navWhite from '@/components/nav'
  import footerBlack from '@/components/footer'
  export default {
    name: 'activities',
    components: { navWhite, footerBlack },
    props: ['activityid'],
    data () {
      return {
        x: ''
      }
    },
    methods: {
      init: function () {
        window.scrollTo(0, 0)
        this.$http.get('activity_detail/' + this.activityid)
        .then(function (response) {
          this.x = response.data
        })
      }
    },
    watch: {
      $route: 'init'
    },
    created () {
      this.init()
    }
  }
</script>

<style scoped>
.activity {
  background-color: #f8f8f8;
  min-height: 100vh;
  overflow: hidden;
  width: 100%;
  position: relative;
  padding-bottom: 120px;
  padding-top: 100px;
  box-sizing: border-box;
}
.main{
  margin-top: 60px;
  margin: auto;
  width: 90%;
  background-color: white; 
  height: 1000px;
  text-align: center;
}
.main img {
  max-width: 80%;
}
@media screen and (max-width: 768px) {
  .main {
    width: 100%;
  }
  .main img {
    width: 100%;
    float: none;
  }
}
</style>


