import Vue from 'vue'
import Router from 'vue-router'
import VueResource from 'vue-resource'
import join from '@/components/join'
import works from '@/components/works'
import team from '@/components/team'
import index from '@/components/index'
import aboutUs from '@/components/aboutUs'
import activities from '@/components/activities'
import test from '@/components/sendMessage'
import activity from '@/components/activity'

Vue.use(Router)
Vue.use(VueResource)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/works',
      name: 'works',
      component: works
    },
    {
      path: '/team',
      name: 'team',
      component: team
    },
    {
      path: '/aboutUs',
      name: 'aboutUs',
      component: aboutUs
    },
    {
      path: '/join',
      name: 'join',
      component: join
    },
    {
      path: '/test',
      name: 'test',
      component: test
    },
    {
      path: '/activities',
      name: 'activities',
      component: activities
    },
    {
      path: '/activity/:id',
      name: 'activity',
      component: activity
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



