<template>
			<div class="works" id="works">
				<p class="works_cn">项目案例</p>
				<p class="works_en">Macture works</p>
				<!-- <div></div> -->
				<hr/>
				<a class="work" v-for="(work, index) in works" v-if="index < 6"  v-bind:href="work.link" target="_blank">
					<img class="work_pic" v-bind:src="work.pic"/>
					<p class="work_name">{{work.name}}</p>
					<p class="work_desc">{{work.desc}}</p>
				</a>
			</div>
</template>

<script >
  export default {
    name: 'indexProject',
    data () {
      return {
        works: []
      }
    },
    methods: {
      init () {
        this.$http.get('project')
        .then(function (response) {
          this.works = response.data
        })
      }
    },
    watch: {
      $route: 'init'
    },
    created () {
      this.init()
    }
  }
</script>

<style scoped>
.works {
	background-color: #F8F8F8;
	width: 86%;
	overflow: hidden;
	text-align: center;
	padding: 30px 7%;
}
.works_cn {
	color: #2d3b3e;
	font-size: 20px;
	margin: 0;
}
.works_en {
	color: #2d3b3e;
	font-size: 17px;
	margin: 0;
}
.works hr{
	width: 50px;
	height: 2px;
	border: 0;
	background: -webkit-linear-gradient(left, #05D5EE, #0081E5);
	background: -o-linear-gradient(right, #05D5EE, #0081E5); 
	background: -moz-linear-gradient(right, #05D5EE, #0081E5); 
	background: linear-gradient(to right, #05D5EE , #0081E5); 
}
.work {
	background-color: white;
	width: 30%;
	overflow: hidden;
	float: left;
	margin: 30px 1.16%;
	text-align: left;
	padding-bottom: 10px;
	display: block;
	box-shadow: 0px 1px 2px #eeeeee;
}
@media screen and (max-width: 990px){
	.work{
		width: 45%;
	}
}
@media screen and (max-width: 540px){
	.work{
		width: 100%;
		margin: 30px 0;
	}
	.works{
		padding: 30px 0;
		width: 100%;
	}
}
.work_pic {
	width: 100%;
	margin: 0 auto;
}
.work_name {
	font-size: 18px;
	color: #555555;
	margin: 5px 10px;
}
.work_desc {
	font-size: 15px;
	color: #555555;
	margin: 5px 10px;
}
</style>


