<template>
			<div class="xuezhang">
				<ul class="xuezhang_tags">
					<li @click="showMessage($index)" v-for="(message, $index) in messages" :class="{active: active == $index}">
						<p class="xuezhang_tag" v-bind:id="'xuezhang_tag'+$index"><span v-if="$index === active">&lt;</span><span>{{message.tag}}</span><span v-if="$index === active">&gt;</span></p>
					</li>
				</ul>
          <div class="text">
            <div class="xuezhang_pic">
              <img class="circle1" src="../assets/circle1.png" @click="last"/>
              <img class="xuezhang_pic_img" v-bind:src="message.pic"/>
              <p class="xuezhang_pic_name">&lt;{{message.tag}}&gt;</p>
            </div>
            <div class="message message_more" id="message_more"  @mouseleave="more = false" v-if="more">
              <img class="circle2" src="../assets/circle2.png" @click="next"/>
              <!-- <img class="message_bg" src="../assets/message.jpg"/> -->
               <div class="message_text">
                <p class="more_1"><span class="more_name">{{message.name}}</span><span class="more_group">{{message.group}}</span></p>
                <p class="more_school">{{message.grade}} {{message.school}}</p>
                <p class="more_duty">
                  <span v-if="message.type == -1">原</span><span v-if="message.type == 1">现</span>
                  <span v-if="message.type != 0">{{message.duty}}</span>
                  <span>{{message.now}}</span></p>
                <p class="more_xzjy">学长寄语：</p>
                <p class="more_text">{{message.text}}</p>
              </div>
            </div>
            <div class="message message_less" id="message_less" v-else>
              <img class="circle2" src="../assets/circle2.png" @click="next"/>
              <!-- <img class="message_bg" src="../assets/message.jpg"/> -->
              <div class="message_text">
                <p class="less_text" v-bind:title="message.text">{{message.text}}</p>
                <p class="less_name">{{message.name}}，{{message.group}}，{{message.now}}</p>
                <button class="message_button" @click="more = true"><span class="button_value">more</span></button>
              </div>
            </div>
          </div>
			</div>
</template>

<script>
  export default {
    name: 'indexMessage',
    data () {
      return {
        messages: [
          // {
          //   tag: 'guox',
          //   name: '郭翔',
          //   pic: require('../assets/twt_xuezhang.jpg'),
          //   group: '前端开发',
          //   grade: '2013届',
          //   school: '机械学院',
          //   now: '饿了么',
          //   duty: '天外天站长',
          //   type: -1,
          //   text: '在天外天家在这里能收获更多。'
          // }
        ],
        active: 0,
        message: {},
        more: false
      }
    },
    methods: {
      init: function () {
        this.$http.get('personal_info')
        .then(function (response) {
          this.messages = response.data
        })
        this.message = this.messages[0]
      },
      showMessage: function (i) {
        this.active = i
        this.message = this.messages[this.active]
      },
      next: function () {
        this.active = (this.active + 1) % this.messages.length
        this.message = this.messages[this.active]
      },
      last: function () {
        this.active = (this.active + this.messages.length - 1) % this.messages.length
        this.message = this.messages[this.active]
      }
    },
    watch: {
      $route: 'init'
    },
    created () {
      this.init()
    }
  }
</script>

<style scoped>
.xuezhang {
	color: white;
	background-color: black;
	overflow: hidden;
  width: 100%;
  position: relative;
}
.xuezhang_tags {
	list-style: none;
	overflow: hidden;
	margin: 0;
	padding: 0 5%;
	width: 100%;
	background-color: #f0f2f2
}
.xuezhang_tags li{
	float: left;
	text-align: center;	
	width: 18%;
	min-width: 50px;
	line-height: 4vw;
	font-size: 2vw;
	padding: 1vw 0;
	color: #779ea3;
	cursor: pointer;
}
.xuezhang_tag{
	display: inline;
}
.active p{
	color: #308ee6;
	border-bottom: 2px solid #9ccbed;
}
.text {
  color: white;
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 29.42vw;
}
.xuezhang_pic {
  width: 35.9%;
  height: 100%;
  position: absolute;
  top:0;
  left: 0;
  float: left;
  overflow: hidden;
}
.xuezhang_pic_img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.xuezhang_pic_name {
  position: absolute;
  width: 100%;
  text-align: center;
  margin: 0;
  font-size: 4vw;
  top: 0;
  height: 100%;
}
.xuezhang_pic_name:after {
  display:inline-block;
  width:0;
  height:100%;
  vertical-align:middle;
  content:"";
}
.message {
  width: 64.1%;
  height: 29.42vw;
  position: relative;
  float: right;
  overflow: hidden;
  background: url("../assets/message.jpg")
}
.message_text {
  position: absolute;
  top: 0;
  width: 80%;
  margin: 11% 10%;
}
.less_text {
  width: 100%;
  margin: 0;
  margin-top: 4vw;
  font-size: 2.5vw;
  font-weight: 300;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.less_name {
  width: 100%;
  margin: 1vw 0;
  font-size: 1.2vw;
  font-weight: 300;
}
.message_button{
  -webkit-border-radius: 1.5vw;
  -moz-border-radius: 1.5vw;
  -o-border-radius: 1.5vw;
  border-radius: 1.5vw;
  height: 3vw;
  width: 10vw;
  background: transparent;
  color: white;
  font-size: 1.6vw;
  font-weight: 200;
  border: 2px solid #5cdcf3;
  margin: 0;
  padding: 0.8vw 0;
  min-height: 20px;
  min-width: 50px;
  outline: none;
  cursor: pointer;
  transition: all 0.5s;
}
.message_button:hover {
  box-shadow: 0px 0px 20px white;
}
.button_value {
  position: relative;
  bottom: 0.6vw;
  text-align: center;
  margin: 0;
}
#message_more p{
  overflow: hidden;
  font-weight: 300;
  margin-bottom: 1.5%;
}
.more_1 {
  margin-bottom: 0;
  padding: 0;
}
.more_name {
  font-size: 2vw;
  margin-right: 1.5vw;
  font-weight: 400;
}
.more_group {
  font-size: 1vw;
}
.more_school {
  margin: 0;
  padding: 0;
  font-size: 1.2vw;
}
.more_duty {
  margin: 0;
  padding: 0;
  font-size: 1.2vw;
}
.more_xzjy {
  margin: 0;
  padding: 0;
  font-size: 1vw;
  margin-top: 3%;
}
.more_text {
  margin: 0;
  padding: 0;
  font-size: 1.7vw;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.circle1 {
  position: absolute;
  right: 0;
  top: 41%;
  height: 14%;
  cursor: pointer;
  z-index: 2;
}
.circle2{
  position: absolute;
  left: 0;
  top: 41%;
  height: 14%;
  cursor: pointer;
  z-index: 2;
}
@media screen and (max-width: 768px){
  .message {
    height: 80vw;
  }
  .xuezhang_tags {
    min-height: 40px;
  }
  .xuezhang_tags li{
    padding: 3vw 0;
    min-width: 50px;
    line-height: 10vw;
    font-size: 4vw;
  }
  .xuezhang_pic_name {
    display: none;
  }
  .xuezhang_pic{
    width: 100%;
    height: 80vw;
  }
  .xuezhang_pic img {
    object-fit: cover;
  }
  .text {
    height: 80vw;
  }
  .message {
    width: 100%;
    height: 80vw;
    background: transparent;
  }
  .message_button{
    border: 1px solid #f8f8f8;
    -webkit-border-radius: 3vw;
    -moz-border-radius: 3vw;
    -o-border-radius: 3vw;
    border-radius: 3vw;
    width: 18vw;
    height: 6vw;
    cursor: pointer;
  }
  .button_value {
    font-size: 3vw;
  }
  .less_text {
    font-size: 5vw;
    -webkit-line-clamp: 3;
    margin-top: 15vw;
  }
  .less_name{
    font-size: 3vw;
  }
  .message_text {
    margin: 10% 10%;
  }
  .more_name {
    font-size: 5vw;
  }
  .more_group {
    font-size: 2.5vw;
  }
  .more_school {
    font-size: 3vw;
  }
  .more_duty {
    font-size: 3vw;
  }
  .more_xzjy {
    font-size: 2.5vw;
  }
  .more_text {
    font-size: 4.25vw;
  }
  .circle1 {
    display: none;
  }
  .circle2 {
    display: none;
  }
}
</style>


