<template>
    <div class="indexTop" id="top">
      <div class="nav" id="nav">
        <div class="navButton" @click="show = !show"></div>
        <router-link :to="{path: '/index'}" id="nav-logo"><img src="../assets/twtlogo.png"></router-link>
        <div class="nav-list" :class="{show: show}">
          <ul>
            <li><router-link :to="{path: '/index'}" class="nav-tag">首页</router-link></li>
            <li><router-link :to="{path: '/aboutUs'}" class="nav-tag">关于我们</router-link></li>
            <li><router-link :to="{path: '/works'}" class="nav-tag">项目案例</router-link></li>
            <li><router-link :to="{path: '/activities'}" class="nav-tag">活动动态</router-link></li>
            <li><router-link :to="{path: '/team'}" class="nav-tag">团队成员</router-link></li>
            <li id="special"><router-link :to="{path: '/join'}" class="nav-tag">加入我们</router-link></li>
          </ul>
        </div>
      </div>
      <hr/>
      <div :class="{'hide':show === false}" class="fade" @click="show = false"></div>
      <indexTopPicture :activities="activities"></indexTopPicture>
    </div>
</template>

<script>
  import indexTopPicture from '@/components/indexTopPicture'
  export default {
    name: 'indexTop',
    components: {indexTopPicture},
    data () {
      return {
        activities: [
          // {
          //   title: '这是活动名称',
          //   desc: '这是活动描述',
          //   place: '这是活动时间和地点',
          //   pic: require('../assets/photo1.jpg')
          // },
          // {
          //   title: '这是活动名称2',
          //   desc: '这是活动描述2',
          //   place: '这是活动时间和地点2',
          //   pic: require('../assets/photo2.jpg')
          // }
        ],
        show: false
      }
    },
    mounted () {
      window.addEventListener('scroll', this.scrollRun) // 监听滚动
    },
    methods: {
      scrollRun: function () {
        // console.log(this.status)
        this.show = false
      },
      init: function () {
        this.$http.get('advertise')
        .then(function (response) {
          this.activity.title = response.data[0].title
          this.activity.desc = response.data[0].desc
          this.activity.place = response.data[0].place
        })
      }
    },
    watch: {
      $route: 'init'
    },
    created () {
      this.init()
    }
  }
</script>

<style scoped>
  .navButton {
    background-clip: content-box;
    width: 20px;
    height: 4px;
    border-top: 4px solid #00a1e9;
    border-bottom: 4px solid #00a1e9; 
    background-color: #00a1e9; 
    padding: 4px 0; 
    display: none;
  }
  .nav{
    width: 100%;
    height: 60px;
    font-size: 13px;
    background: transparent;
    color: white;
    position: absolute;
    top: 0;
    z-index: 4;
  }
  .nav img{
    float: left;
    margin-left: 5%;
    height: 100%;
    width: auto;
  }
  .nav-list{
    float: right;
    margin-right: 5%;
    width: 500px;
    z-index: 4
  }
  .nav-list ul{
    margin-top: 20px;
    border: 0;
    padding: 0;
    list-style: none;
    overflow: hidden;
  }
  .nav-list li{
    height: 19px;
    float: left;
    width: 16.66%;
    text-align: center;
    position: relative;
  }
  .nav-list a{
    line-height: 13px;
    transition: color 0.5s ease-in-out;
    -moz-transition: color 0.5s ease-in-out;
    -webkit-transition: color 0.5s ease-in-out; 
    -o-transition: color 0.5s ease-in-out; 
  }
  .nav-list a:hover{
    color: #05D5EE;
  }
  .nav-tag {
    color: white;
  }
  .router-link-active {
    color: #05D5EE;
  }
  @media screen and (max-width: 768px){
    .nav img {
      margin-left: 0;
    }
    .navButton {
      display: block;
      margin: 20px 5% 0 5%;
      float: left;
      cursor: pointer;
    }
    .nav-list a:hover{
      color: #00a1e9;
    }
    .nav-tag {
      color: black;
    }
    .router-link-active {
      color: #00a1e9;
    }
    .nav-list {
      position: absolute;
      left: 0;
      top: 60px;
      width: 100%;
      background-color: white;
      box-shadow: 0px 5px 15px rgba(0,0,0,0.15);
      padding: 0;
      height: 0;
      opacity: 0;
      overflow: hidden;
      transition:all 0.5s;
      -moz-transition:all 0.5s; /* Firefox 4 */
      -webkit-transition:all 0.5s; /* Safari and Chrome */
      -o-transition:all 0.5s; /* Opera */
    }
    .show{
      height: 200px;
      opacity: 100;
    }
    .nav-list ul {
      width: 100%;
      margin: 0;
      padding: 10px 0;
    }
    .nav-list li {
      width: 100%;
      display: block;
      height: 29px;
      transition: all .3s;  
      border-bottom: 1px solid #f0f2f2;
    }
    #special {
      border-bottom: 0;
    }
    .nav-list a{
      line-height: 30px;
    }
  }
  .fade {
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.3);
    position: fixed;
    top: 0;
    z-index: 1;
      transition:all 0.5s;
      -moz-transition:all 0.5s; /* Firefox 4 */
      -webkit-transition:all 0.5s; /* Safari and Chrome */
      -o-transition:all 0.5s; /* Opera */
  }
  .hide {
    background-color: rgba(0,0,0,0);
    z-index: -1;
  }
.indexTop hr {
  height: 1px;
  width: 90%;
  position: absolute;
  top: 60px;
  left: 5%;
  margin: 0;
  background-color: rgba(255, 255, 255, 0.3);
  border: 0;
  z-index: 2;
}
</style>


