<template>
		<div class="works" id="works">
			<navWhite></navWhite>
			<div class="banner">
				<img v-bind:src="banner"/>
			</div>
			<div class="main">
				<p class="type">
					<span @click="show(2)" id="type2">全部</span>
					&frasl;
					<span @click="show(0)" id="type0">APP</span>
					&frasl;
					<span @click="show(1)" id="type1">WEB</span>
				</p>
				<hr>
				<div class="works_container">
					<a class="work" v-for="(work, index) in works" v-bind:id="'work'+index" v-if="index <= num[nowType] && (work.type == 3 || work.type == nowType || nowType == 2)" v-bind:href="work.link" target="_blank">
						<div class="work_pic">
							<img v-bind:src="work.pic"/>
						</div>
						<p class="work_name">{{work.name}}</p>
						<p class="work_desc">{{work.desc}}</p>
					</a>
					<a class="work" v-for="(work, index) in works" v-bind:id="'work'+index" v-if="index > num[nowType] && (work.type == 3 || work.type == nowType || nowType == 2)" v-show="showMore" v-bind:href="work.link" target="_blank">
						<div class="work_pic">
							<img v-bind:src="work.pic"/>
						</div>
						<p class="work_name">{{work.name}}</p>
						<p class="work_desc">{{work.desc}}</p>
					</a>
					<p class="more" @click="showMore = true" v-if="(num[nowType] < works.length - 1) && (!showMore)" >查看更多</p>
				</div>
			</div>
			<footerBlack></footerBlack>
		</div>
</template>

<script>
  import navWhite from '@/components/nav'
  import footerBlack from '@/components/footer'
  export default {
    name: 'works',
    components: { navWhite, footerBlack },
    data () {
      return {
        works: [],
        banner: require('../assets/works_banner.jpg'),
        showMore: false,
        nowType: 2,
        num: [0, 0, 0]
      }
    },
    methods: {
      show: function (i) {
        document.getElementById('type' + this.nowType).style.color = 'black'
        document.getElementById('type' + i).style.color = '#00a1e9'
        this.nowType = i
        this.showMore = false
      },
      init: function () {
        window.scrollTo(0, 0)
        this.$http.get('project')
        .then(function (response) {
          this.works = response.data
        })
        this.getNum()
        this.showMore = false
      },
      getNum: function () {
        var napp = 0
        var nweb = 0
        this.num[0] = this.num[1] = this.works.length
        this.num[2] = 11
        for (var i = 0; i < this.works.length; i++) {
          if (this.works[i].type === 0 || this.works[i].type === 3) {
            napp++
            if (napp === 12) this.num[0] = i
          }
          if (this.works[i].type === 1 || this.works[i].type === 3) {
            nweb++
            if (nweb === 12) this.num[1] = i
          }
        }
        // console.log(this.num)
        // console.log(this.works.length - 1)
        // console.log(this.num[2] < this.works.length - 1)
      }
    },
    watch: {
      $route: 'init'
    },
    created () {
      this.init()
    }
  }
</script>

<style scoped>
.works {
	background-color: #F8F8F8;
	min-height: 100vh;
	overflow: hidden;
	width: 100%;
	position: relative;
	padding-bottom: 80px;
	box-sizing: border-box;
}
.banner {
	width: 100%;
	margin-top: 60px;
}
.banner img {
	width: 100%;
}
.main {
	width: 86%;
	padding: 30px 7%;
	text-align: center;
}
#type2 {
	color: #00a1e9;
}
.main hr{
	width: 40px;
	height: 2px;
	border: 0;
	background: -webkit-linear-gradient(left, #05D5EE, #0081E5);
	background: -o-linear-gradient(right, #05D5EE, #0081E5); 
	background: -moz-linear-gradient(right, #05D5EE, #0081E5); 
	background: linear-gradient(to right, #05D5EE , #0081E5); 
	margin: 0 auto
}
.type{
	font-size: 13px;
	margin:  10px auto;
	cursor: pointer;
}
.works_container {
	overflow: hidden;
	transition: height 1s;
	-moz-transition: height 1s;
	-webkit-transition: height 1s; 
	-o-transition: height 1s; 
	text-align: center;
}
.work {
	display: block;
	background-color: white;
	width: 30%;
	overflow: hidden;
	float: left;
	margin: 20px 1.16%;
	text-align: left;
	padding-bottom: 10px;
	box-shadow: 0px 3px 5px #dddddd;
}
@media screen and (max-width: 990px){
	.work{
		width: 45%;
		margin: 20px 2.5%;
	}
}
@media screen and (max-width: 540px){
	.work{
		width: 100%;
		margin: 20px 0;
	}
	.main {
		width: 100%;
		padding: 30px 0;
	}
}
.work_pic {
	margin: 0 auto;
	width: 100%;
	padding-bottom: 61.44%;
	position: relative;
}
.work img {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	margin: 0
}
.work_name {
	font-size: 15px;
	color: #555555;
	margin-top: 15px;
	margin-left: 20px;
}
.work_desc {
	font-size: 10px;
	color: #555555;
	margin-top: 15px;
	margin-left: 20px;
}
.more {
	color: #999999;
	float: left;
	margin: auto;
	width: 100%;
	text-align: center;
	font-size: 12px;
	cursor: pointer;
}
@media screen and (max-width: 768px) {
	.works {
		padding-bottom: 80px;
	}
}
</style>


