<template>
	<div class="team" id="team">
		<navWhite></navWhite>
		<div class="members">
			<div class="cell2">
				<div class="cell blue"></div>
				<teamMember :member="member[0]"></teamMember>
				<div class="cell blue"></div>
			</div>
			<div class="cell2">
				<teamMember :member="member[1]"></teamMember>
				<div class="cell blue"></div>
				<teamMember :member="member[2]"></teamMember>
			</div>
			<div class="cell2">
				<div class="cell blue"></div>
				<teamMember :member="member[3]"></teamMember>
				<div class="cell blue"></div>
			</div>
			<div class="cell2 cell2_big" >
				<teamMember :member="member[4]"></teamMember>
				<div class="cell blue"></div>
				<teamMember :member="member[5]"></teamMember>
				<div class="cell blue"></div>
				<teamMember :member="member[6]"></teamMember>
				<div class="cell blue"></div>
			</div>
			<div class="member_logo">
				<img src="../assets/member_logo1.png"/>
			</div>
			<div class="cell2 cell2_big" id="special">
				<teamMember :member="member[7]"></teamMember>
				<div class="cell blue"></div>
				<teamMember :member="member[8]"></teamMember>
				<div class="cell blue"></div>
				<div class="cell change" @click="change"><img src="../assets/member_change.png"/></div>
				<div class="cell blue"></div>
			</div>
			<div class="cell2 hide">
				<teamMember :member="member[9]"></teamMember>
				<div class="cell blue"></div>
				<teamMember :member="member[10]"></teamMember>
			</div>
			<div class="cell2 hide">
				<div class="cell blue"></div>
				<teamMember :member="member[11]"></teamMember>
				<div class="cell blue"></div>
			</div>
			<div class="cell2 hide">
				<teamMember :member="member[12]"></teamMember>
				<div class="cell blue"></div>
				<teamMember :member="member[13]"></teamMember>
			</div>
			<div class="cell2 hide">
				<div class="cell blue"></div>
				<teamMember :member="member[14]"></teamMember>
				<div class="cell blue"></div>
			</div>
			<div class="cell2 hide">
				<teamMember :member="member[15]"></teamMember>
				<div class="cell blue"></div>
				<teamMember :member="member[16]"></teamMember>
			</div>
			<div class="cell2 hide">
				<div class="cell blue"></div>
				<teamMember :member="member[17]"></teamMember>
				<div class="cell blue"></div>
			</div>
		</div>
		<div class="message">
			<p class="title">寄语动态</p>
			<p v-for="message in messages"><span>{{message.name}}</span>：{{message.text}}</p>
			<p><span>……</span></p>
		</div>
		<sendMessage></sendMessage>
		<footerBlack></footerBlack>
	</div>
</template>

<script>
  import navWhite from '@/components/nav'
  import footerBlack from '@/components/footer'
  import sendMessage from '@/components/sendMessage'
  import teamMember from '@/components/teamMember'
  export default {
    name: 'team',
    components: { navWhite, footerBlack, sendMessage, teamMember },
    data () {
      return {
        member: [
          // {
          //   name: '某某某',
          //   major: '某某专业',
          //   grade: '201x级',
          //   group: '某某组',
          //   pic: require('../assets/member_linshi.jpg')
          // }
        ],
        messages: []
      }
    },
    methods: {
      change: function () {
        this.$http.get('member')
        .then(response => {
          this.member = response.data
        })
      },
      init: function () {
        window.scrollTo(0, 0)
        this.$http.get('member')
        .then(response => {
          this.member = response.data
        })
        this.$http.get('message_board')
        .then(function (response) {
          this.messages = response.data
        })
      }
    },
    watch: {
      $route: 'init'
    },
    created () {
      this.init()
    }
  }
</script>

<style scoped>
#nav-team {
	color: #00a1e9;
}
.members{
  width: 100%;
  overflow: hidden;
  margin-top: 60px;
  position: relative;
  background-color: #0081e5;
}
.cell2 {
  float: left;
  width: 33.33%;
  margin: 0;
  border: 0;
  padding: 0;
  overflow: hidden;
  background-color: #0081e5;
  height: 11.11vw;
}
.cell2_big {
  height: 22.22vw;
}
.cell{
  float: left;
  width: 33.33%;
  margin: 0;
  padding: 0;
  border: 0;
  position: relative;
  overflow: hidden;
  padding-bottom: 33.33%;
}
.team{
	background-color: #f8f8f8;
	width: 100%;	
	min-height: 100vh;
	overflow: hidden;
	position: relative;
	padding-bottom: 80px;
	box-sizing: border-box;	
}
.blue {
  background-color: #05d5ee;
}
.member_logo {
  overflow: hidden;
  float: left;
  width: 33.33%;
  background-color: #0081e5;
  padding-bottom: 22.2%;
  position: relative;
}
.member_logo img{
  width: 60%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.change { 
  background-color: #0081e5;
}
.change img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: block;
  cursor: pointer;
}
@media screen and (max-width: 768px){
  .cell2 {
    width: 100%;
    height: 33.33vw;
  }
  .cell2_big {
    height: 66.66vw;
  }
  .member_logo{
    width:100%;
    padding-bottom: 66.66%;
    position: absolute;
    top: 0;
  }
  .hide {
    display: none;
  }
}
.message {
	width: 70%;
	margin: 50px 10%;
	padding: 20px 5%;
	background-color: white;
	font-size: 14px;
	font-weight: 300;
	line-height: 25px;
}
.title {
	font-size: 20px;
	font-weight: 400;
	text-align: center;
}
.message span{
	font-weight: 400;
}
@media screen and (max-width: 768px) {
	.team {
		padding-bottom: 80px;
	}
}
</style>


